/** @jsx React.DOM */

React = require("react");

var ImageForm = React.createClass({displayName: 'ImageForm',
  propTypes: {
    onAddUrl: React.PropTypes.func.isRequired
  },

  getInitialState: function() {
    return { url: '' };
  },

  render: function() {
    return (
      React.DOM.form( {onSubmit:this.onSubmit}, 
        React.DOM.input( {type:"text", placeholder:"Enter URL", size:"30",
               value:this.state.url, onChange:this.handleChange} ),
        React.DOM.input( {type:"submit", value:"Add URL"} )
      )
    );
  },

  handleChange: function(e) {
    this.setState({url: e.target.value});
  },

  onSubmit: function(e) {
    e.preventDefault();
    this.props.onAddUrl(this.state.url);
    this.setState({url: ''});
  }
});

module.exports = ImageForm;
